import React, { useEffect, useRef } from 'react';
import kurentoUtils from 'kurento-utils';

const PARTICIPANT_MAIN_CLASS = 'participant main';
const PARTICIPANT_CLASS = 'participant';

const App = () => {
  const ws = useRef(null);
  const participants = useRef({});
  const name = useRef(null);
  const room = useRef(null);

  useEffect(() => {
    ws.current = new WebSocket('wss://focusing.site/signal');

    ws.current.onmessage = function (message) {
      var parsedMessage = JSON.parse(message.data);
      console.info('Received message: ' + message.data);

      switch (parsedMessage.id) {
        case 'existingParticipants':
          onExistingParticipants(parsedMessage);
          break;
        case 'newParticipantArrived':
          onNewParticipant(parsedMessage);
          break;
        case 'participantLeft':
          onParticipantLeft(parsedMessage);
          break;
        case 'receiveVideoAnswer':
          receiveVideoResponse(parsedMessage);
          break;
        case 'iceCandidate':
          participants.current[parsedMessage.name].rtcPeer.addIceCandidate(parsedMessage.candidate, function (error) {
            if (error) {
              console.error("Error adding candidate: " + error);
              return;
            }
          });
          break;
        default:
          console.error('Unrecognized message', parsedMessage);
      }
    };

    return () => {
      ws.current.close();
    };
  }, []);

  const register = () => {
    name.current = document.getElementById('name').value;
    room.current = document.getElementById('roomName').value;

    // document.getElementById('room-header').innerText = 'ROOM ' + room.current;
    // document.getElementById('join').style.display = 'none';
    // document.getElementById('room').style.display = 'block';

    const message = {
      id: 'join',
      name: name.current,
      room: room.current,
    };
    sendMessage(message);
  };

  const onNewParticipant = (request) => {
    receiveVideo(request.name);
  };

  const receiveVideoResponse = (result) => {
    participants.current[result.name].rtcPeer.processAnswer(result.sdpAnswer, function (error) {
      if (error) return console.error(error);
    });
  };

  // function callResponse(message) {
  //   if (message.response !== 'accepted') {
  //     console.info('Call not accepted by peer. Closing call');
  //     stop();
  //   } else {
  //     webRtcPeer.processAnswer(message.sdpAnswer, function (error) {
  //       if (error) return console.error(error);
  //     });
  //   }
  // }
  
  function onExistingParticipants(msg) {
    var constraints = {
      audio: true,
      video: {
        mandatory: {
          maxWidth: 320,
          maxFrameRate: 15,
          minFrameRate: 15,
        },
      },
    };
    console.log(name.current + ' registered in room ' + room.current);
    var participant = new Participant(name.current);
    participants.current[name.current] = participant;
    var video = participant.getVideoElement();
  
    var options = {
      localVideo: video,
      mediaConstraints: constraints,
      onicecandidate: participant.onIceCandidate.bind(participant),
    };
    participant.rtcPeer = new kurentoUtils.WebRtcPeer.WebRtcPeerSendonly(options, function (error) {
      if (error) {
        return console.error(error);
      }
      this.generateOffer(participant.offerToReceiveVideo.bind(participant));
    });
  
    msg.data.forEach(receiveVideo);
  }
  
  function leaveRoom() {
    sendMessage({
      id: 'leaveRoom',
    });
  
    for (var key in participants.current) {
      participants.current[key].dispose();
    }
  
    document.getElementById('join').style.display = 'block';
    document.getElementById('room').style.display = 'none';
  
    ws.current.close();
  }
  
  function receiveVideo(sender) {
    var participant = new Participant(sender);
    participants.current[sender] = participant;
    var video = participant.getVideoElement();
  
    var options = {
      remoteVideo: video,
      onicecandidate: participant.onIceCandidate.bind(participant),
    };
  
    participant.rtcPeer = new kurentoUtils.WebRtcPeer.WebRtcPeerRecvonly(options, function (error) {
      if (error) {
        return console.error(error);
      }
      this.generateOffer(participant.offerToReceiveVideo.bind(participant));
    });
  }
  
  function onParticipantLeft(request) {
    console.log('Participant ' + request.name + ' left');
    var participant = participants.current[request.name];
    participant.dispose();
    delete participants.current[request.name];
  }
  
  function sendMessage(message) {
    var jsonMessage = JSON.stringify(message);
    console.log('Sending message: ' + jsonMessage);
    ws.current.send(jsonMessage);
  }

  function Participant(name) {
    this.name = name;
    var container = document.createElement('div');
    container.className = isPresentMainParticipant() ? PARTICIPANT_CLASS : PARTICIPANT_MAIN_CLASS;
    container.id = name;
    var span = document.createElement('span');
    var video = document.createElement('video');
    var rtcPeer;
  
    container.appendChild(video);
    container.appendChild(span);
    container.onclick = switchContainerClass;
    document.getElementById('participants').appendChild(container);
  
    span.appendChild(document.createTextNode(name));
  
    video.id = 'video-' + name;
    video.autoplay = true;
    video.controls = false;
  
    this.getElement = function () {
      return container;
    };
  
    this.getVideoElement = function () {
      return video;
    };
  
    function switchContainerClass() {
      if (container.className === PARTICIPANT_CLASS) {
        var elements = Array.prototype.slice.call(document.getElementsByClassName(PARTICIPANT_MAIN_CLASS));
        elements.forEach(function (item) {
          item.className = PARTICIPANT_CLASS;
        });
  
        container.className = PARTICIPANT_MAIN_CLASS;
      } else {
        container.className = PARTICIPANT_CLASS;
      }
    }
  
    function isPresentMainParticipant() {
      return document.getElementsByClassName(PARTICIPANT_MAIN_CLASS).length !== 0;
    }
  
    this.offerToReceiveVideo = function (error, offerSdp, wp) {
      if (error) return console.error('sdp offer error');
      console.log('Invoking SDP offer callback function');
      var msg = {
        id: 'receiveVideoFrom',
        sender: name,
        sdpOffer: offerSdp,
      };
      sendMessage(msg);
    };
  
    this.onIceCandidate = function (candidate, wp) {
      console.log('Local candidate' + JSON.stringify(candidate));
  
      var message = {
        id: 'onIceCandidate',
        candidate: candidate,
        name: name,
      };
      sendMessage(message);
    };
  
    Object.defineProperty(this, 'rtcPeer', { writable: true });
  
    this.dispose = function () {
      console.log('Disposing participant ' + this.name);
      this.rtcPeer.dispose();
      container.parentNode.removeChild(container);
    };
  }

  return (
    <div>
      <input type="text" id="name" placeholder="Enter your name" />
      <input type="text" id="roomName" placeholder="Enter room name" />
      <button onClick={register}>Register</button>
      <button onClick={leaveRoom}>Leave Room</button>
      <div id='participants'>
      {Object.values(participants).map((participant) => (
        <div key={participant.name}>
          <video ref={participant.getVideoElement} autoPlay controls={false}></video>
          <span>{participant.name}</span>
        </div>
      ))}
      </div>
    </div>
  );
};

export default App;